@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-size: 1vw;
}

.container {
  margin: 0 auto;
  position: relative;
}

.prop-blck {
  width: 100%;
  position: relative;
}

.prop-blck::before {
  content: '';
  display: block;
}

.prop-blck > * {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.prop-blck--2-3::before {
  padding-top: calc(3 / 2 * 100%);
}

.prop-blck--3-2::before {
  padding-top: calc(2 / 3 * 100%);
}

.prop-blck--4-3::before {
  padding-top: calc(3 / 4 * 100%);
}

.prop-blck--3-4::before {
  padding-top: calc(4 / 3 * 100%);
}

.prop-blck--16-9::before {
  padding-top: calc(9 / 16 * 100%);
}

.prop-blck--9-16::before {
  padding-top: calc(16 / 9 * 100%);
}

.prop-blck--1_85-1::before {
  padding-top: calc(1 / 1.85 * 100%);
}

.prop-blck--1-1_85::before {
  padding-top: calc(1.85 / 1 * 100%);
}

.prop-blck--2_39-1::before {
  padding-top: calc(1 / 2.39 * 100%);
}

.prop-blck--1-2_39::before {
  padding-top: calc(2.39 / 1 * 100%);
}

header {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3.64583vw;
}

header::before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  display: block;
  pointer-events: none;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  position: absolute;
  left: 0;
  top: 0;
}

header .flex-block {
  padding: 0 1.71875vw 0 1.5625vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  height: 100%;
}

header .flex-block .yana-logo {
  width: 10.9375vw;
}

header .flex-block .yana-logo img {
  display: block;
  width: 100%;
}

header .flex-block .super-logo {
  width: 6.30208vw;
}

header .flex-block .super-logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

header .flex-block .mobile-menu {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 7.1875vw;
  height: 5vw;
  margin-left: -3.75vw;
  margin-top: -2.5vw;
  cursor: pointer;
  display: none;
}

header .flex-block .mobile-menu svg {
  width: 100%;
  height: 100%;
  display: block;
}

header .flex-block .mobile-menu svg rect {
  position: relative;
  transition: 0.3s ease;
}

header .flex-block .mobile-menu svg rect:nth-child(1) {
  transform-origin: 50% 50%;
}

header .flex-block .mobile-menu svg rect:nth-child(3) {
  transform-origin: 50% 50%;
}

header .flex-block .mobile-menu.active svg rect:nth-child(1) {
  transform: rotate(45deg) translateY(45%);
}

header .flex-block .mobile-menu.active svg rect:nth-child(2) {
  opacity: 0;
}

header .flex-block .mobile-menu.active svg rect:nth-child(3) {
  transform: rotate(-45deg) translateY(-45%);
}

header .mobile-menu-container {
  display: none;
  height: calc(100vh - (46/320 * 100vw));
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  width: 100%;
  box-sizing: border-box;
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
}

header .mobile-menu-container .header-menu {
  flex-grow: 1;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

header .mobile-menu-container .header-menu a {
  width: 100%;
  display: block;
  transition: 0.3s ease;
  color: #ffffff;
  font-weight: 900;
  font-size: 6.25vw;
  line-height: 15.625vw;
  text-align: center;
}

header .mobile-menu-container .header-menu a:active {
  background: rgba(255, 255, 255, 0.1);
}

header .mobile-menu-container .social-container {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: 21.875vw;
  box-sizing: border-box;
}

header .mobile-menu-container .social-block {
  width: 56.25vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header .mobile-menu-container .social-block .social-link {
  display: block;
  cursor: pointer;
  width: 11.25vw;
  height: 11.25vw;
  background: transparent;
  margin: 0;
  padding: 0;
  border: none;
}

header .mobile-menu-container .social-block .social-link svg {
  width: 100%;
  display: block;
}

header .mobile-menu-container .social-block .social-link:active svg path {
  fill: #E9B382;
}

